.become-host-container{
    margin-top: 60px;
    color:rgb(255, 255, 255) ;
    
    .details{
        height: 100%;
        position: absolute;
        align-items: flex-start;
        padding: 0 80px;
        top: 0;
        z-index: 1;
        max-width: 500px;
    }
    
    .host{
        position: relative;
         flex-wrap: wrap;
         
         h1{
             font-size: 3rem;
             line-height: 3.25rem;
             font-weight: 300;
             margin: 0;
            }

            p{
                font-size: 1.2rem;
                line-height: 24px;
                font-weight: 100;
                padding-top: 12px
            }
            
            img{
                object-fit: cover;
             height: 500px;
             border-radius: 15px;
         }

        .more-btn{
         margin: 20px 0 0 0;
         padding: 15px 30px;
         color: #222222;
         background-color: rgb(255, 255, 255);
         font-weight: 600;
         border-radius: 5px;
         font-size: 1rem;
         letter-spacing: 1px;
        }
    }
}